import axios from 'axios'
import { useEffect } from 'react'
import { Button } from 'antd'
import getConfig from 'next/config'

const { publicRuntimeConfig } = getConfig()



const api = require('../lib/api')

function Index ({ userRepos, userStaredRepos, isLogin }) { 
  if (!isLogin) {
    return <div className="root">
      <p>You haven't login yet</p>
      <Button type="primary" href={publicRuntimeConfig.OAUTH_URL}>Login</Button>
      <style jsx>{`
        .root {
          height: 400px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
      `}</style>
    </div>
  }
}
Index.getInitialProps = async ({ctx, reduxStore}) => {
  // const result =   await  axios.get('/github/search/repositories?q=react')
  //     .then(resp => console.log(resp))

  // ctx.req and ctx.res only works when it is run on server
  const user = reduxStore.getState().user
  if (!user || !user.id) {
    return {
      isLogin: false,
    }
  }
  const userRepos = await api.request(
    {
      // url: '/search/repositories?q=react',
      url: '/user/repos',
    },
    ctx.req,
    ctx.res
  )
  const userStaredRepos = await api.request({
    url: '/user/starred',
  }, ctx.req, ctx.res)
  return {
    // data: result.data
    isLogin: true,
    userRepos: userRepos.data,
    userStaredRepos: userStaredRepos.data,
  }
}

export default Index